.header {

    .logo-box {
        position: fixed;
        text-align: center;
        @include fluid('top', 20, 50);
        @include fluid('left', 20, 50);

        &__img {
            @include fluid('height', 40, 60);
            filter: drop-shadow(1rem 1rem 2rem $shadow-2);

        }

        &__heading {
            @include fluid('font-size', 14, 20);
            font-family: 'Dosis', sans-serif;
            text-shadow: 0 .5rem 1rem $shadow-4;
            font-weight: 800;
        }
    }

}