* {
    margin: 0;
    padding: 0;
    box-sizing: inherit;


    // ? testing
    // border: 1px solid red;
}

html {
    box-sizing: border-box;
    font-size: 62.5%;

    @include respond(media-sm) {
        font-size: 50%;
    }
}

body {
    box-sizing: border-box;
    font-weight: 400;
    font-size: $default-text-size;
    font-family: 'VerbReg', sans-serif;
    color: $dirty-white;
    @include fluid('font-size', 18, 28);

    background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);

    background-image: linear-gradient(to right bottom, rgba(#29323c, 0.7), rgba(#485563, 1)), url('https://images.unsplash.com/photo-1505238680356-667803448bb6?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1950&q=80');
}

::-webkit-scrollbar {
    display: none;
}