.img-comp {
    position: relative;
    @include fluid('margin-top', 0, -80);

    &__img {
        position: absolute;
        filter: drop-shadow(0 1rem 3rem $shadow-2);
        @include fluid('height', 130, 450);
        transition: all .3s;
        outline-offset: 1rem;

        &-p1 {
            @include fluid('top', 50, 20);
            @include fluid('left', 50, 20);
            transform: rotate(35deg);
        }

        &-p2 {
            @include fluid('top', 10, -50);
            @include fluid('right', 90, 30);
            transform: rotate(-15deg);
            z-index: 1;
        }

        &-p3 {
            @include fluid('top', 60, 100);
            @include fluid('right', 80, 150);
            transform: rotate(15deg);
        }

        &:hover {
            transform: scale(1.2) rotate(30deg);
        }
    }
}