// FONTS

// Normal
@font-face {
    font-family: 'VerbReg';
    src: url('fonts/verbcondregular-webfont.eot');
    src: url('fonts/verbcondregular-webfont.eot?#iefix') format('embedded-opentype'),
        url('fonts/verbcondregular-webfont.woff') format('woff'),
        url('fonts/verbcondregular-webfont.ttf') format('truetype'),
        url('fonts/verbcondregular-webfont.svg#verbcondregular') format('svg');
    font-weight: normal;
    font-style: normal;
}

// Italic
@font-face {
    font-family: 'VerbLt';
    src: url('fonts/verblight-webfont.eot');
    src: url('fonts/verblight-webfont.eot?#iefix') format('embedded-opentype'),
        url('fonts/verblight-webfont.woff') format('woff'),
        url('fonts/verblight-webfont.ttf') format('truetype'),
        url('fonts/verblight-webfont.svg#verblight') format('svg');
    font-weight: normal;
    font-style: normal;

}

// Black
@font-face {
    font-family: 'VerbBlack';
    src: url('fonts/Verb-Black.eot');
    src: url('fonts/Verb-Black.eot?#iefix') format('embedded-opentype'),
        url('fonts/Verb-Black.woff2') format('woff2'),
        url('fonts/Verb-Black.woff') format('woff'),
        url('fonts/Verb-Black.ttf') format('truetype'),
        url('fonts/Verb-Black.svg#Verb-Black') format('svg');
    font-weight: 900;
    font-style: normal;
}

// TYPOGRAPHY STYLES

.primary-heading {

    &--top {
        display: block;
        font-family: 'Dosis', sans-serif;
        text-shadow: 0 .5rem 1rem $shadow-4;
        @include fluid('font-size', 35, 80);
        @include fluid('margin-bottom', 5, 15);

        &::after {
            content: '';
            display: block;
            width: 35%;
            height: 3px;
            margin-top: 1.3rem;
            background-color: $color-1;
            box-shadow: 0 .3rem 1rem $shadow-8;
        }
    }

    &--bottom {
        display: block;
        font-family: 'Dosis', sans-serif;
        @include fluid('font-size', 18, 40);
        font-weight: 400;
    }
}